@include wrap-class {
	line-height: 1;

	.sui-col-auto {
		flex: 0 0 auto;
		width: auto;
	}

	@include media(max-width, md) {

		[class*=sui-col-md-] {
			margin-bottom: 0;
		}
	}

	[class*="sui-icon-"] {
		vertical-align: middle;
	}

	.sui-modal-content {
		.sui-box .sui-box-footer {
			justify-content: space-between;
		}

		.sui-tabs.sui-tabs-flushed {
			margin-top: 0 !important;
			border-top: 1px solid $tabs-content-border-color;
		}
	}

	.sui-tabs:not(.sui-side-tabs) {

		> .sui-tabs-menu {

			> .sui-tab-item {
				display: flex;
				align-items: center;

				> .sui-tag {
					margin-left: 5px;
				}
			}
		}
	}

	.sui-tag.sui-tag-inactive {
		color: $color-dark-alt;

		/* TODO: Revisit when the latest version of SUI is used */
		@include high_contrast {
			color: #fff;
		}
	}

	.sui-box-body {
		img.sui-image.sui-upsell-image {
			margin-bottom: -$space;
		}
	}

	.sui-upsell-row {

		.sui-upsell-notice:first-child {
			padding: 0;
		}
	}

	.sui-box .sui-box-header {
		// Dashboard sitemap widget
		> span.sui-icon-loader {
			font-size: 18px;
			margin-left: $space-xs;
			margin-top: 1px;
		}
	}

	.sui-accordion {

		&-item {

			&:first-child,
			&:last-child {
				.sui-accordion-item-header {
					border-radius: 0;
				}
			}

			&.sui-accordion-item--error {
					 box-shadow: 0 2px 2px 0 rgb(255, 109, 109) !important;
					 border-radius: 4px;
		 }

			&-header {
				position: relative;
				background: $white;
				flex-wrap: wrap;

				.sui-accordion-col-auto {

					&:last-child {
						margin-left: auto;
					}
				}

				.sui-button-icon [class*=sui-icon-] {
					margin: 0 !important;
				}

				@include media(max-width, md) {
					padding-top: $space-xs;
					padding-bottom: $space-xs;

					@for $i from 1 through 12 {
						.sui-accordion-col-#{$i} {
							flex: 1 0 0;
						}
					}

					.sui-accordion-col,
					.sui-accordion-col-auto {

						&:not(:first-child) {
							padding-right: 19px;
						}
					}
				}
			}

			&-body {

				> .sui-table {
					background: $white;
				}
			}

			&-action.sui-dropdown {

				[class*="sui-icon-"]:not(.sui-icon-loader) {
					margin-right: 0;
				}
			}

		}
	}

	.sui-toggle {

		.sui-toggle-label {
			display: flex;
			align-items: center;

			> .sui-tag {
				margin-left: 5px;
			}
		}

		.sui-toggle-content {
			margin-top: 10px;
		}

	}

	.sui-recipient {
		span {
			line-height: 29px;
		}
	}

	.sui-insert-variables {

		&-label {
			margin-right: 40px;
		}

		textarea ~ select.sui-variables + .sui-select {
			.selection span[role=combobox] {
				border: 0;
			}
		}
	}

	.sui-tag-pro {
		z-index: 3;
		cursor: pointer;
	}

	.wds-notice-wrap {
		@extend .sui-notice-dismiss;
	}

	.sui-border-frame > small strong {
		margin-bottom: 10px;
		display: inline-block;
	}

	.sui-form-field {
		.sui-error-message {
			display: none;
		}

		&.sui-form-field-error {
			.sui-error-message {
				display: block;
			}
		}

		.sui-form-control-group {
			@extend .sui-form-control;
			display: flex;
			align-items: center;
			padding: 0;
			height: auto;

			.sui-form-control,
			.sui-select2-control {
				flex: 1 0 0;
			}

			select.sui-select + .sui-select {
				width: 0 !important;
			}

			.sui-form-control,
			select.sui-select + .sui-select .selection span[role=combobox].select2-selection--single,
			select.sui-select + .sui-select .selection span[role=combobox].select2-selection--multiple {

				&,
				&:hover,
				&:focus {
					border: 0;
					background: transparent;
					box-shadow: none;
					border-radius: 0;
				}
			}

			.sui-field-prefix {
				margin-left: $space-xxs;
				margin-right: 0;
			}

			.sui-field-suffix {
				margin-left: 0;
				margin-right: $space-xxs;
			}

			.sui-field-prefix,
			.sui-field-suffix {

				> [class*=sui-icon-]:first-child {
					display: flex;
				}
			}
		}

		> .sui-side-tabs {
			margin: 0;
		}
	}

	.sui-box-body .sui-upsell-row .sui-upsell-notice p a.sui-button-purple {
		color: #fff;
		display: table;
		margin-top: 10px;
	}

	.sui-box.sui-summary:last-child {
		margin-bottom: $space;
		background-image: none;
	}

	.sui-summary:not(.sui-unbranded) {

		.sui-summary-image-space {
			background-image: url($wds-summary-image);
			background-size: $wds-summary-image--width $wds-summary-image--height;
			background-position: center;
			background-repeat: no-repeat;

			@include media(min-width, md) {
				display: block;
			}
		}
	}

	.sui-upgrade-page-header__image {
		background-size: 90%;
	}

	.sui-notice .sui-notice-content .sui-notice-message p {
		margin-top: 0;
	}

	.sui-table {
		.sui-tooltip {
			margin-top: 15px;
		}

		th, td {
			word-break: break-all;
		}

		@include media('max-width', md) {
			.sui-tooltip {
				margin-top: 5px;
			}
		}
		@include media('max-width', sm) {

			thead, tbody, tr, th, td {
				display: block;
			}

			.sui-tooltip {
				margin-top: -20px !important;
				margin-bottom: 20px !important;
			}
			tbody {

				tr {
					border-bottom: 1px solid $table--border-color;
				}

				td {
					padding-left: $space-s;
					padding-right: $space-s;
					border: 0;
					height: auto;
				}
			}
		}
	}

	.sui-label {

		.sui-label-light {
			font-weight: 300;
			white-space: pre;
		}
	}

	@include high_contrast {
		.sui-button-ghost.sui-button-icon {
			padding-left: 4px;
			padding-right: 4px;
		}
	}

	.sui-box-settings-row {

		.sui-box-settings-col-1 {

			.sui-settings-label {
				display: flex;
				align-items: center;

				.sui-tag {
					margin-left: 5px;
				}
			}
		}
	}

	.sui-pagination-navbar {
		display: flex;
		align-items: center;
		flex-wrap: wrap;

		.sui-pagination-options {
			display: flex;
			align-items: center;
			color: $pagination-results;
			font: 400 13px/22px $font;
			letter-spacing: $font--letter-spacing;
			margin-right: $space-xs;

			.sui-select2-control {
				min-width: 80px;
				margin: 0 $space-xs;

				select.sui-select + .sui-select .selection span[role=combobox].select2-selection--single {

					.select2-selection__rendered {
						font-size: 12px;
						line-height: 16px;
						padding-top: 7px;
						padding-bottom: 7px;
					}

					.select2-selection__arrow {
						height: 28px;
					}
				}
			}
		}

		.sui-pagination-wrap {
			flex: 1 0 0;
		}
	}


	.sui-select.sui-select-dropdown-container--open .sui-select-dropdown .select2-results .select2-results__options .select2-results__option.select2-results__option--disabled {
		background-color: palette(silver, default);
	}


}

.sui-select.sui-select-dropdown-container--open .sui-select-dropdown .select2-results .select2-results__options .select2-results__option.select2-results__message {
	padding-left: 20px !important;

	&:before {
		display: none !important;
	}
}

span.select2-container.sui-select.sui-select-dropdown-container--open {
	z-index: 99999;
}

#{$sui-class} .sui-select.sui-select-dropdown-container--open {

	.sui-select-theme--vars .sui-select-dropdown .select2-results .select2-results__options .select2-results__option[role=option] {
		padding-top: $space-xxs;
		padding-bottom: $space-xxs;

		.sui-variable-name {
			min-width: auto;
			line-height: 1.6;
		}
	}

	.sui-search-name {
		display: block;
		line-height: 22px;
		padding-top: 5px;
	}

	.sui-search-url {
		display: block;
		font: 500 10px/12px $font;
		letter-spacing: -0.25px;
		padding-bottom: 5px;
	}
}
