@include wrap-class('.wds-page-settings') {
	.tab_general_settings {

		#wds-plugin-modules {

			.sui-notice.sui-notice-warning {
				margin-bottom: $sui-gutter;
			}
		}

		.wds-confl-plugs-modal {

			> .sui-box {

				> .sui-box-body {

					> .sui-description,
					> .sui-notice {
						margin-bottom: $space-m;

						&:last-child {
							margin: 0;
						}
					}
				}
			}
		}

		.wds-confl-plug-list {
			margin-left: -$sui-gutter;
			margin-right: -$sui-gutter;
			margin-bottom: -$sui-gutter;
			padding: $sui-gutter;
			background: palette(silver, light);
			overflow-y: auto;
			max-height: 360px;

			> .wds-conf-plug-item {
				text-align: left;
				margin-bottom: $space-xs;

				&:last-child {
					margin-bottom: 0;
				}

				> .sui-box {
					display: flex;
					align-items: center;
					color: palette(gray, dark);
					font-size: $font-size-s;
					font-weight: 500;
					margin: 0;
					padding: $space-s $space-m;
					box-shadow: 0 0 0 1px $table--border-color;

					> [class^="sui-icon"] {
						margin-right: $space-xs;
					}

					> .sui-button-icon {
						margin-left: auto;
					}
				}
			}
		}

		.wds-verification-tags {
			.sui-settings-label {
				color: $color-dark-alt;
			}

			.sui-description {
				margin-bottom: $space-xs;
			}

			.wds-custom-meta-tag {
				margin-bottom: $space-s;
			}
		}

		.wds-analysis-strategy-tabs label {
			width: 110px;
			height: 110px;
			justify-content: center;
			align-items: flex-end;
			display: flex;
			background-repeat: no-repeat;
			background-position: center 20px;

			&.wds-strategy {
				&-strict {
					background-image: url("#{$image-path}graphic-settings-seoengine-smart-inactive.svg");

					&.active {
						background-image: url("#{$image-path}graphic-settings-seoengine-smart-active.svg");
					}
				}

				&-moderate {
					background-image: url("#{$image-path}graphic-settings-seoengine-wide-inactive.svg");

					&.active {
						background-image: url("#{$image-path}graphic-settings-seoengine-wide-active.svg");
					}
				}

				&-loose {
					background-image: url("#{$image-path}graphic-settings-seoengine-all-inactive.svg");

					&.active {
						background-image: url("#{$image-path}graphic-settings-seoengine-all-active.svg");
					}
				}

				&-manual {
					background-image: url("#{$image-path}graphic-settings-seoengine-manual-inactive.svg");

					&.active {
						background-image: url("#{$image-path}graphic-settings-seoengine-manual-active.svg");
					}
				}
			}
		}
	}

	.tab_user_roles {
		.select2 {
			margin-top: $space-xs;
			margin-bottom: $space;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.tab_import_export {
		.sui-upload {
			margin: $space-xs 0;
		}

		.sui-table {
			td:last-child {
				text-align: right;
			}
		}

		.wds-separator-top {
			@include gutter('top');
			@include margin('top');
		}

		#wds-import-status {

			.wds-import-footer {
				@include widen();
				@include pull('left');
				@include gutter('top');
				@include gutter('right');
				@include gutter('left');
				@include margin('top');
				border-top: 1px solid $color-border-grey;

				button {
					float: left;
				}

				.wds-import-main-action {
					float: right;
				}

				.wds-notice {
					@include gutter('top');
				}
			}
		}

		.wds-advanced-import-options {
			@include cf;

			> span {
				background: $color-table-border;
				padding: 6px 7px 5px 6px;
				display: table;
				cursor: pointer;
				float: right;
				margin-top: $space-xs;
				font-size: $font-size-s;
				border-radius: 3px;

				&:before {
					content: "+";
					margin-right: 2px;
				}
			}

			.wds-separator-top.wds-import-item:first-of-type {
				margin-top: 0;
			}

			&-inner {
				clear: both;
				display: none;
			}

			&.open {
				> span {
					border-radius: 3px 3px 0 0;

					&:before {
						content: "-";
					}
				}

				.wds-advanced-import-options-inner {
					display: block;
				}
			}
		}
	}

	.tab_data {
		.sui-box-settings-col-2 {
			.sui-side-tabs.sui-tabs,
			.sui-button-red {
				margin-top: 10px;
			}
		}
	}
}
