@include wrap-class {

	#tab_url_redirection {

		.wds-vertical-tab-section:not(:last-child) {
			@include margin('bottom');
		}

		.wds-maxmind-upsell-notice {
			color: palette(purple, default);

			&:hover {
				text-decoration: underline;
			}
		}

		.sui-modal {
			.sui-box-body {
				.sui-form-field {
					.sui-description {
						text-align: left;
					}
				}

				[data-modal-close] {
					float: left;
				}

				.wds-action-button {
					float: right;
				}

				@include cf;
			}
		}

		.wds-no-redirects-message {
			text-align: center;
		}

		.wds-redirect-controls {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: $sui-gutter-md;
			padding: 0 $sui-gutter-md;

			> .sui-checkbox,
			> .sui-button {
				margin-right: $space-xs;
				margin-bottom: $space-xs;
			}

			> .sui-search {
				margin-left: auto;
				margin-bottom: $space-xs;
			}
		}

		.sui-box-builder {
			border-radius: 0;
			border-width: 0;

			@include pull('left');
			@include pull('bottom');
			@include widen;
			@include margin('top');

			.sui-box-builder-header {
				padding-top: 0;
				padding-bottom: $sui-gutter-md;

				.sui-box-builder-actions {
					display: flex;
					justify-content: space-between;
					flex-wrap: wrap;

					> button {
						margin-bottom: $space-xs;
					}

					&:not(:last-child) {
						margin-bottom: $sui-gutter-md;
					}
				}
			}

			.sui-box-builder-footer {
				padding: $sui-gutter-md $sui-gutter-md $space-xs $sui-gutter-md;

				@include media(min-width, md) {
					padding: $sui-gutter $sui-gutter $sui-gutter-md $sui-gutter;
				}
			}

			.sui-pagination-navbar {

				> div[class*="sui-pagination-"] {
					margin-bottom: $space-xs;
				}
			}

			.wds-redirect-item {
				$parent: &;
				display: flex;
				align-items: center;
				padding-left: 20px;
				padding-right: 20px;

				small {
					margin-bottom: 0;
				}

				&.wds-redirect-item-columns {
					small {
						margin-bottom: 5px;
					}
				}

				&.wds-redirect-has-title {
					.wds-redirect-item-checkbox,
					.wds-redirect-item-source,
					.wds-redirect-item-destination, {
						align-self: baseline;
					}
				}

				&-columns {
					align-items: baseline;
				}

				&-checkbox {
					flex-basis: 25px;
					width: 25px;
					min-width: 25px;
				}

				&-source {
					flex-basis: 30%;
					width: 30%;
					min-width: 30%;

					display: flex;
					flex-direction: column;
					align-items: flex-start;
				}

				.sui-tooltip {
					display: block;
					max-width: 90%;
				}

				&-source-trimmed {
					color: #333;
					font: 500 13px/22px "Roboto", Arial, sans-serif;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					max-width: fit-content;
				}

				&-destination {
					flex-basis: 40%;
					width: 40%;
					min-width: 40%;

					@include media(min-width, md) {
						flex-basis: 45%;
						width: 45%;
						min-width: 45%;
					}

					small {
						display: block;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						max-width: 90%;
					}
				}

				&-options {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					flex: 1 0 0;

					[class*=sui-icon-] {
						display: inline-flex;
						margin: 5px;
					}

					.sui-tooltip.sui-tooltip-constrained:after {
						white-space: pre-line;
					}
				}

				&-dropdown {
					flex: 0 0 auto;
				}

				.sui-tag {
					margin-top: 5px;
					margin-bottom: 5px;
				}
			}
		}

		#wds-import-redirects-modal {
			.sui-form-field,
			.sui-label {
				margin-bottom: 10px;
			}

			.sui-notice-error {
				margin: 10px 0;
			}
		}
	}

	.wds-redirect-to-opt {
		text-align: left;

		.sui-tooltip {
			margin-left: $space-xs;
		}
	}

	.wds-redirect-rules-container {
		width: calc(100% + $sui-gutter * 2);
		margin-left: -$sui-gutter;
		margin-bottom: -$sui-gutter;
		padding: $sui-gutter;
		background: palette(silver, light);

		.sui-accordion {
			border: 0;

			.sui-accordion-item {
				border: 0;
				margin-bottom: $space-xs;
				box-shadow: $box-selector--box-shadow;

				&:last-child {
					margin-bottom: 0;
				}

				&-title {

					.sui-icon-link:before {
						color: $color-tag-yellow;
					}
				}

				.sui-accordion-item-header,
				.sui-accordion-item-body {
					background: $white;
				}

				.sui-accordion-item-body {
					border-top: 1px solid palette(silver, soft);

					> .sui-box {
						box-shadow: none;

						> .sui-box-body {
							padding-right: 0;
							padding-left: 0;
							padding-bottom: 0;
						}
					}
				}
			}
		}
	}
}
