@use "sass:math";

@import "advanced-tools/redirects";

@include wrap-class('.wds-page-autolinks') {

	.sui-box-settings-row {

		&.sui-flushed {
			background: #f8f8f8;
			padding-top: 20px;
			border-bottom: 0;

			@media (min-width: 783px) {
				padding-top: 30px;
			}
		}
	}

	.sui-field-list {
		background: #ffffff;
	}

	.sui-modal-content {
		.sui-label {
			justify-content: space-between;
		}
	}

	.wds-keyword-pairs {
		margin-top: 10px;
		margin-bottom: 10px;

		tbody {

			td:last-child {
				text-align: right;

				.sui-modal {
					.sui-box {
						text-align: left;
					}
				}
			}
		}
	}

	[href="#remove"] {
		color: $color-red;

		span.sui-icon-trash:before {
			color: $color-red;
		}
	}

	.wds-postlist-list {
		table.wds-postlist,
		.wds-notice {
			margin-top: $space-xs;
			margin-bottom: $space-xs;
		}

		table.wds-postlist {
			&.wds-postlist-empty_list {
				display: none;
			}
		}

		.wds-postlist-item-remove {
			text-align: right;
		}

		.wds-notice {
			display: none;

			&.wds-postlist-empty_list {
				display: block;
			}
		}
	}

	.wds-breadcrumb-previews {
		font-size: $table--text-font-size;
		line-height: $table--text-line-height;
	}

	.wds-breadcrumb-preview {
		display: flex;
		padding: 5px 0px;

		&-label {
			width: 100%;
			max-width: 70px;
		}

		&-snippets {

			strong {
				color: $info;
				text-decoration: underline;
			}
			.prefix{
				margin-right:10px;
			}

			.sui-icon {
				font-size: 13px;
				margin: 0 $space-xxs;
			}
		}

		&-expander {

			&-text {
				text-decoration: underline;
			}

			.sui-icon {
				margin-left: $space-xxs;

				&:before {
					color: $info;
					font-size: $form--label-font-size;
				}
			}
		}
	}

	.wds-custom-separator {
		margin-top: $space-xs;
	}

	#tab_automatic_linking {

		#tab-content--post_types {
			.sui-box-settings-header {
				margin-bottom: 0 !important;
			}
		}
	}

	#tab_moz {
		.wds-disabled-component {
			padding-bottom: 0;
		}

		.wds-moz-fields {
			background: $color-bg-grey;

			@include pull('left');
			@include widen;
			@include gutter;

			p {
				text-align: center;
			}

			&-inner {
				@include cf;
				max-width: 300px;
				margin: 0 auto;

				input[type="submit"] {
					margin-right: 0;
					float: right;
				}
			}
		}

		.wds-moz-signup-notice {
			text-align: center;
		}

		.sui-wrap {
			margin-right: 0;
			margin-left: 0;
		}

		.sui-table {
			td {
				padding-top: $space-s;
				padding-bottom: $space-s;
			}
		}
	}

	.tab_robots_editor {
		.wds-disabled-component {
			.wds-notice {
				text-align: left;

				&, p {
					margin-bottom: 0;
				}
			}
		}

		textarea[readonly] {
			color: $color-dark-alt;
			font-family: monospace;
			border: 0;
			font-weight: 500;
			padding: $sui-gutter-md;

			&:hover,
			&:focus {
				background: $color-input-bg;
			}
		}

		#robots-preview,
		#robots-file-contents {
			height: 120px;
			min-height: 120px;
			overflow: hidden;
			transition: none;
			resize: none;
		}

		#robots-file-contents {
			min-height: 220px;
		}

		div.sui-box-settings-row:last-of-type {
			border-bottom: none;
		}

		.sui-box-footer {
			@include pull('top');
			@include pull('right');
			@include pull('bottom');
			@include pull('left');
		}

		.wds-auto-sitemap-addition {
			@include margin('top');
		}
	}

	#tab_woo {
		.sui-box-body > p {
			@include gutter('bottom');
			@include margin('bottom');
			border-bottom: 1px solid $color-border-grey;
		}

		.sui-notice.sui-notice-info {
			margin-left: 44px;
			margin-top: -20px;
		}

		.sui-box-footer {
			display: flex;
			justify-content: space-between;
			width: 100%;
		}
	}

	#wds-loc-rules-settings .sui-box-settings-row {
		margin-bottom: $sui-gutter;
		padding-bottom: $sui-gutter;
		border-bottom: 1px solid palette(silver, soft);
	}
}
