@include wrap-class {

	.wds-url-input-wrapper {
		position: relative;

		.wds-url-search-results {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			padding: 10px 0;
			background-color: $white;
			border: 1px solid $select-results--border-color;
			box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.07);
			border-radius: $border-radius;
			z-index: 1;

			.wds-url-search-item {
				cursor: pointer;
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				color: $gray;
				padding: 5px 20px;

				&:hover {
					background-color: $box-selectors--background;
				}

				&.selected,
				&.selected:hover {
					color: $white;
					background-color: palette(gray, light);
				}

				.wds-url-search-item-info {
					flex: 1 0 0;
					overflow: hidden;

					.wds-url-search-item-title {
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
						font-size: 13px;
						line-height: 20px;
					}

					.wds-url-search-item-url {
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
						font-size: 10px;
						line-height: 15px;
					}
				}

				.wds-url-search-item-shortcut {
					flex: 0 0 auto;
					font-size: 12px;
					line-height: 16px;
					font-weight: 500;
					margin-left: 10px;
					padding: 2px 5px;
					border: 1px solid currentColor;
					border-radius: 2px;
				}
			}

			.wds-url-search-results {
				padding: 10px 0;
			}

			.wds-url-search-no-result,
			.wds-url-search-loading {
				color: map-get($button-background, default);
				font-size: 13px;
				line-height: 22px;
				margin: 0;
				padding: 10px 20px;
				background: palette(silver, default);
			}
		}

		.wds-url-input-selected {
			display: flex;
			align-items: center;

			> span:first-child {
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;
				max-width: calc(100% - 30px);
			}

			button {
				margin-left: auto;
			}
		}
	}
}
